import React from 'react'
import { AppBar, Button, Toolbar } from '@mui/material';
import { useNavigate } from 'react-router-dom';

export const Bar = () => {
  const navigate = useNavigate()

  return (
    <>
      <AppBar>
        <Toolbar>
          <Button color='inherit' 
                  onClick={() => navigate('/')}
          >
             
          </Button>
          <Button color='inherit' 
                  onClick={() => navigate('favorite')}
          >
             
          </Button>
        </Toolbar>
      </AppBar>
    </>
  )
}
